html {
  font-family: 'Avenir', 'Arial', 'Microsoft YaHei', -apple-system, sans-serif;
  font-size: 12px;
  line-height: 24px;
  color: white;
}

body {
  margin: 0;
}

.app-container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto
}

@media (min-width: 576px) {
  .app-container {
      max-width:540px
  }
}

@media (min-width: 768px) {
  .app-container {
      max-width:720px
  }
}

@media (min-width: 992px) {
  .app-container {
      max-width:960px
  }
}


